@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700;800&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Cormorant+Garamond&display=swap");

:root {
  --yellow: #cc8f58;
}

#root {
  overflow-x: hidden;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Montserrat";
}

body {
  background-color: #1e1e1e;
  position: relative;
  color: #eeeded;
  position: relative;
}

body::before {
  content: "";
  position: absolute;
  top: 0;
  left: 93.5px;
  width: 25%;
  height: 100%;
  border-left: 1px solid #eeeded;
  border-right: 1px solid #eeeded;
  opacity: 0.1;
  z-index: -1;
}

body::after {
  content: "";
  position: absolute;
  top: 0;
  right: 93.5px;
  width: 25%;
  height: 100%;
  border-left: 1px solid #eeeded;
  border-right: 1px solid #eeeded;
  opacity: 0.1;
  z-index: -1;
}

/* #watch {
  cursor: url("./assets/images/play.png"), auto;
} */

a {
  color: inherit;
  text-decoration: none;
}

#map {
  width: 100%;
  height: 400px;
  -webkit-filter: invert(1) grayscale(1); /* Safari 6.0 - 9.0 */
  filter: invert(1) grayscale(1);
}

.hidden {
  overflow: hidden;
}

.yellow {
  color: var(--yellow);
}

.hidden {
  overflow: hidden;
}

.container {
  width: 600vw;
  height: 100vh;
  display: flex;
  flex-wrap: wrap;
}

#linkDiv {
  height: 0;
  width: 100%;
  position: fixed;
  bottom: 0;
  left: 0;
  overflow: hidden;
  z-index: 12;
}

#linkDiv::before {
  content: "";
  position: absolute;
  top: 0;
  left: 93.5px;
  width: 25%;
  height: 100%;
  border-left: 1px solid #eeeded;
  border-right: 1px solid #eeeded;
  opacity: 0.1;
  z-index: -1;
}

#linkDiv::after {
  content: "";
  position: absolute;
  top: 0;
  right: 93.5px;
  width: 25%;
  height: 100%;
  border-left: 1px solid #eeeded;
  border-right: 1px solid #eeeded;
  opacity: 0.1;
  z-index: -1;
}

@media (max-width: 768px) {
  body::before {
    content: "";
    position: absolute;
    top: 0;
    left: 20px;
    width: 25%;
    height: 100%;
    border-left: 1px solid #eeeded;
    border-right: 1px solid #eeeded;
    opacity: 0.1;
    z-index: -1;
  }

  body::after {
    content: "";
    position: absolute;
    top: 0;
    right: 20px;
    width: 25%;
    height: 100%;
    border-left: 1px solid #eeeded;
    border-right: 1px solid #eeeded;
    opacity: 0.1;
    z-index: -1;
  }
}

@media (max-width: 768px) {
  #linkDiv::before {
    left: 40px;
  }

  #linkDiv::after {
    right: 40px;
  }
}

.slider-wrap {
  width: 100vw;
  height: 100vh;
  position: relative;
}

#slider {
  position: relative;
  width: 100%;
  height: 100vh;
  overflow: hidden;
}

.slide {
  position: absolute;
  inset: 0;
  overflow: hidden;
}

.slide img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.navigation {
  position: absolute;
  top: 0;
  height: 100vh;
  width: 50%;
  opacity: 0;
  z-index: 10;
}

#next,
#nextfac {
  right: 0;
  cursor: url("./assets/images/arrow-next.webp"), auto;
}

#prev,
#prevfac {
  cursor: url("./assets/images/arrow-prev.webp"), auto;
}

#nextfac {
  right: 0;
  width: 20%;
}

#prevfac {
  width: 20%;
}

.slider h2 {
  font-size: clamp(1rem, 5vw, 5rem);
  font-weight: 400;
  text-align: center;
  letter-spacing: 0.5em;
  text-transform: uppercase;
  margin-right: -0.5em;
  color: hsl(0, 0, 80%);
  width: 90vw;
  max-width: 1200px;
}

.slider {
  height: 100%;
  width: 100%;
  top: 0;
  position: absolute;
  visibility: hidden;
}

/* .slider img {
  object-fit: contain;
} */

.outer,
.inner {
  width: 100%;
  height: 100%;
  overflow-y: hidden;
}

.slider .bg {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  object-fit: cover;
  background-size: cover;
  background-position: center;
}

.clip-text {
  overflow: hidden;
}

.first .bg {
  background-image: linear-gradient(
      180deg,
      rgba(0, 0, 0, 0.6) 0%,
      rgba(0, 0, 0, 0.3) 100%
    ),
    url(./assets/images/25.webp);
}

.second .bg {
  background-image: linear-gradient(
      180deg,
      rgba(0, 0, 0, 0.6) 0%,
      rgba(0, 0, 0, 0.3) 100%
    ),
    url("./assets/images/4.webp");
}

html.has-scroll-smooth {
  overflow: hidden;
}

html.has-scroll-dragging {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.has-scroll-smooth body {
  overflow: hidden;
}

.has-scroll-smooth [data-scroll-container] {
  min-height: 100vh;
}

[data-scroll-direction="horizontal"] [data-scroll-container] {
  height: 100vh;
  display: inline-block;
  white-space: nowrap;
}

[data-scroll-direction="horizontal"] [data-scroll-section] {
  display: inline-block;
  vertical-align: top;
  white-space: nowrap;
  height: 100%;
}

.c-scrollbar {
  position: absolute;
  right: 0;
  top: 0;
  width: 11px;
  height: 100%;
  transform-origin: center right;
  transition: transform 0.3s, opacity 0.3s;
  opacity: 0;
}
.c-scrollbar:hover {
  transform: scaleX(1.45);
}
.c-scrollbar:hover,
.has-scroll-scrolling .c-scrollbar,
.has-scroll-dragging .c-scrollbar {
  opacity: 1;
}
[data-scroll-direction="horizontal"] .c-scrollbar {
  width: 100%;
  height: 10px;
  top: auto;
  bottom: 0;
  transform: scaleY(1);
}
[data-scroll-direction="horizontal"] .c-scrollbar:hover {
  transform: scaleY(1.3);
}

.c-scrollbar_thumb {
  position: absolute;
  top: 0;
  right: 0;
  background-color: black;
  opacity: 0.5;
  width: 7px;
  border-radius: 10px;
  margin: 2px;
  cursor: -webkit-grab;
  cursor: grab;
}
.has-scroll-dragging .c-scrollbar_thumb {
  cursor: -webkit-grabbing;
  cursor: grabbing;
}
[data-scroll-direction="horizontal"] .c-scrollbar_thumb {
  right: auto;
  bottom: 0;
}
